import { connect } from "react-redux";
import { actionCreators } from "./store";
import { DetailWrapper, Title, Content } from "./style";
import { useEffect } from "react";

const Detail = (props) => {
  const { title, content, initializeArticleList } = props;
  useEffect(() => {
    initializeArticleList();
  }, []);

  return (
    <DetailWrapper>
      <Title>{title}</Title>
      <Content dangerouslySetInnerHTML={{ __html: content }} />
    </DetailWrapper>
  );
};

const mapState = (state) => ({
  title: state.getIn(["detail", "articleDetailList"]).toJS()[0].title,
  content: state.getIn(["detail", "articleDetailList"]).toJS()[0].content,
});

const mapDispatch = (dispatch) => ({
  initializeArticleList() {
    dispatch(actionCreators.initializeArticleListAction);
  },
});

export default connect(mapState, mapDispatch)(Detail);
